import React from "react";

const Resume = ({ data }) => {
  if (data) {
    var education = data.education.map(function (education) {
      return (
        <div key={education.school}>
          <h3>{education.school}</h3>
          <p className="info">
            {education.degree} <span>&bull;</span>
            <em className="date">{education.graduated}</em>
          </p>
          <p>{education.description}</p>
        </div>
      );
    });
    var work = data.work.map(function (work) {
      return (
        <div key={work.company}>
          <h3>{work.company}</h3>
          <p className="info">
            {work.title}
            <span>&bull;</span> <em className="date">{work.years}</em>
          </p>
          <p>{work.description}</p>
        </div>
      );
    });
    var skills = data.skills.map(function (skills) {
      const skillList=getSkillList(skills.value);
      return (
        <li key={skills.name}>
          <em>{skills.name}</em>
          <p>{skillList}</p>
        </li>
      );
    });

    function getSkillList(skills) {
      return (
        <ol>
          {skills.map(skill => (
            <li key={skill}>{skill}</li>
          ))}
        </ol>
      );
    }

    var accomplishments = data.accomplishments.map((accomplishments)=>{
      return (
       <li>
         <em>{accomplishments.title}</em>
         <p>{accomplishments.desc}</p>
       </li> 
      )
    });
  }

  return (
    <section id="resume">
      <div className="row education">
        <div className="three columns header-col">
          <h1>
            <span>Education</span>
          </h1>
        </div>

        <div className="nine columns main-col">
          <div className="row item">
            <div className="twelve columns">{education}</div>
          </div>
        </div>
      </div>

      <div className="row work">
        <div className="three columns header-col">
          <h1>
            <span>Work</span>
          </h1>
        </div>

        <div className="nine columns main-col">{work}</div>
      </div>

      <div className="row skill">
        <div className="three columns header-col">
          <h1>
            <span>Skills</span>
          </h1>
        </div>

        <div className="nine columns main-col">

          <div className="bars">
            <ul >{skills}</ul>
          </div>
        </div>
      </div>

      
      <div className="row skill">
        <hr></hr>
        <div className="three columns header-col">
          <h1>
            <span>Accomplishments</span>
          </h1>
        </div>

        <div className="nine columns main-col">
          {/* <p>{accomplishments}</p> */}

          <div className="accomplish">
            <ul >{accomplishments}</ul>
          </div>
        </div>
      </div>

      <hr></hr>

      <center>
      <div style={{textalign: 'center'}}>
        <h3>Certificates/Project section Coming Soon...</h3>
      </div>
      </center>

    </section>
  );
};

export default Resume;
